export const timer = document.getElementById('timer');
const days1 = document.getElementById('days1');
const hours1 = document.getElementById('hours1');
const minutes1 = document.getElementById('minutes1');
const seconds1 = document.getElementById('seconds1');
const days2 = document.getElementById('days2');
const hours2 = document.getElementById('hours2');
const minutes2 = document.getElementById('minutes2');
const seconds2 = document.getElementById('seconds2');

const targetDate = new Date(2019, 7, 30, 10, 0, 0, 0);

const SECOND = 1000;
const MINUTE = 60 * SECOND;
const HOUR = 60 * MINUTE;
const DAY = 24 * HOUR;
const YEAR = 365 * DAY;

export const MillisecondsPer = {
    SECOND,
    MINUTE,
    HOUR,
    DAY,
    YEAR,
};

export const getTimeByMilliseconds = (milliseconds) => {
    const years = Math.floor(milliseconds / MillisecondsPer.YEAR);
    const msForYears = MillisecondsPer.YEAR * years;

    const days = Math.floor((milliseconds - msForYears) / MillisecondsPer.DAY);
    const msForDays = msForYears + MillisecondsPer.DAY * days;

    const hours = Math.floor((milliseconds - msForDays) / MillisecondsPer.HOUR);
    const msForHours = msForDays + MillisecondsPer.HOUR * hours;

    const minutes = Math.floor((milliseconds - msForHours) / MillisecondsPer.MINUTE);
    const msForMinutes = msForHours + MillisecondsPer.MINUTE * minutes;

    const seconds = Math.floor((milliseconds - msForMinutes) / MillisecondsPer.SECOND);
    const msForSeconds = msForMinutes + MillisecondsPer.SECOND * seconds;

    const ms = Math.floor((milliseconds - msForSeconds));

    return {years, days, hours, minutes, seconds, ms};
};

export const timeFormatter = (value, type) => {
    switch(type) {
        case 'ms': 
            if (value < 10) {
                value = "0" + String(value);
            }
        
            if (value < 100) {
                value = "0" + String(value);
            }

            return value;
        
        default:
            if (value < 10) {
                value = "0" + String(value);
            }

            return value;
    }
}

function renderDate() {
    const milliseconds = targetDate - new Date();
    days1.innerHTML = '00';
    hours1.innerHTML = timeFormatter(getTimeByMilliseconds(milliseconds).hours);
    minutes1.innerHTML = timeFormatter(getTimeByMilliseconds(milliseconds).minutes);
    seconds1.innerHTML = timeFormatter(getTimeByMilliseconds(milliseconds).seconds);
    days2.innerHTML = '00';
    hours2.innerHTML = timeFormatter(getTimeByMilliseconds(milliseconds).hours);
    minutes2.innerHTML = timeFormatter(getTimeByMilliseconds(milliseconds).minutes);
    seconds2.innerHTML = timeFormatter(getTimeByMilliseconds(milliseconds).seconds);
}

renderDate();
setInterval(renderDate, 1000);